<template>
  <div class="container">
   <ul v-for="(item, index) in list" :key="index">
        <li >
          <span>小区名称：{{item.name}}</span>
          <span>市场均价：{{item.min_price}} - {{item.max_price}} / 元</span>
          <span>建造时间：{{item.time}} 年</span>
          <span>小区位置：{{item.address}}</span>
        </li>
   </ul>
   <!-- <van-loading type="spinner" /> -->
   <div v-if="list.length == 0" class="nodata">没有更多了~ </div> 
   <div class="nodata">❤ 需要更多信息、贷款咨询可以联系我们哦~</div> 
   <button @click="callMe">联系我们</button> 

  </div>
  
</template>

<script>
// Use Vuex
export default {
  data() {
    return {
      list: [],
      nodata: false
    };
  },
  methods: {
    callMe: () => {
      wx.makePhoneCall({
        phoneNumber: '18037461628' //仅为示例，并非真实的电话号码
      })
    }
  },
  onLoad: async function(option) {
    const db = wx.cloud.database();
    // const _ = db.command;
    const { data } = await db
      .collection("hourse")
      .where({ name: (option.value) })
      .get();
    this.list = data;
  }
};
</script>

<style>
.container {
  box-sizing: border-box;
  padding: 0 20px;
  padding-top: 15px;
  min-height: 600px;
  background: #fafafa;
}
ul li {
  display: flex;
  flex-direction:column;
  font-size: 15px;
}
ul li span {
  margin-top: 10px
}

.nodata {
  font-size: 12px;
  color: #666;
  text-align: center;
  margin-top: 40px;
}
button {
  background: #22b37c;
  margin-top: 40px;
  color: #fff;
}
</style>



